import PopupRegister from '@/views/popup/PopupRegister.vue'
import Layout from '@/components/Layout.vue'
import NotFound from '@/404.vue'
import Cookies from 'js-cookie'
Vue.use(VueRouter)

const routes = [
    { path : '/login' , component : () => import('@/views/login/index') , hidden : true },

    {
        path : '/',
        component : Layout,
        redirect : '/helloWorld',
        name : 'HelloWorld',
        hidden : true,
        children : [{
            path : 'helloWorld',
            component : () => import('@/views/HelloWorld')
        }]
    },
    {
        path : '/',
        component : Layout,
        redirect : '/Registration',
        children : [{
            path : 'Registration',
            name : 'Registration',
            component : () => import('@/views/registraion/Registration')
        }]
    },
    {
        path : '/',
        component : Layout,
        redirect : '/PopupRegister',
        children : [{
            path : 'PopupRegister',
            name : 'PopupRegister',
            component : () => import('@/views/popup/PopupRegister')
        }]
    },
    {
        path : '/notice',
        component : Layout,
        children : [
            {
                path : 'list',
                name : 'NoticeList',
                component : () => import('@/views/notice/NoticeList')
            },
            {
                path : 'register',
                name : 'noticeRegister',
                component : () => import('@/views/notice/noticeRegister'),
                // beforeEnter: (to, from, next) => {
                //     console.log(to)
                //     console.log(from)


                //     if(Cookies.get('token')) { //로그인이 유무
                //             if(to.path === '/login') { //로그인 됨 && 로그인 페이지?
                //                 next({path: '/'})  // 로그인 됨 && 로그인 페이지 맞음
                //             } else { // 로그인 됨 && 로그인 페이지 아님
                //                 next() //그럼 그 페이지에 머무르기
                //             }
                //     } else { //로그인이 안됨
                //             if(to.path !== '/login') { //로그인 안됨 && 로그인 페이지가 아님?
                //                 next({path: '/login'}) //로그인 안됨 && 로그인 페이지가 아님
                //             } else { //로그인 안됨 && 로그인 페이지가 맞음
                //                 next() //로그인 페이지
                //             }
                //     }
                // }
            },
            {
                path : 'detail',
                name : 'NoticeDetail',
                component : () => import('@/views/notice/NoticeDetail')
            },
        ]
    },
    

    // {
    //     path: '/',
    //     name: 'HelloWorld',
    //     component: HelloWorld
    // },
    // {
    //     path: '/noticeList',
    //     name: 'NoticeList',
    //     component: NoticeList
    // },
    // {
    //     path: '/noticeDetail',
    //     name: 'NoticeDetail',
    //     component: NoticeDetail
    // },
    // {
    //     path: '/noticeRegister',
    //     name: 'NoticeRegister',
    //     component: NoticeRegister
    // },
    {
        path: '/PopupRegister',
        name: 'PopupRegister',
        component: PopupRegister
    },
    {
        path: '*',
        component: NotFound
    },

]

export default new VueRouter({
  routes
})



